import {Avatar, List, SimpleCell} from "@vkontakte/vkui";
import React from "react";

import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline';

const UserHeaderContent = (p) => {
    let owner = p.user;
    return <List>
        <SimpleCell
            before={<Avatar size={48} src={owner.photo_200} />}
            description="лучший управляющий?">{`${owner.first_name} ${owner.last_name}`}
        </SimpleCell>
        {/*<SimpleCell*/}
        {/*    before={<Icon28MoneyCircleOutline/>}*/}
        {/*    description="счет в Швейцарии"*/}
        {/*>*/}
        {/*    {owner.offshores}*/}
        {/*</SimpleCell>*/}
        {/*<SimpleCell onClick={() => {p.polyClick(owner.area)}} before={<Icon28LocationOutline/>}>*/}
        {/*    {'Управление '+p.areas[owner.area].name}*/}
        {/*</SimpleCell>*/}
    </List>
}

export default UserHeaderContent;